@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap");
.registration_form {
  background-color: #fefefe;
  border: none;
  border-width: 0;
  border-radius: 6px;
  box-shadow: 0 0 0.3rem 0.001rem #999;
  display: flex;
  flex-direction: column;
  font-family: "IBM Plex Sans", sans-serif;
  margin: 2rem;
  padding: 10px 40px 40px 40px; }
  .registration_form .block_group {
    display: block; }
  .registration_form .flex_group {
    display: flex;
    justify-content: space-between; }
  .registration_form ul {
    padding: 0; }
    .registration_form ul li {
      display: flex;
      flex-direction: column;
      list-style: none;
      padding: 0; }
  .registration_form input[type=text], .registration_form input[type=email], .registration_form input[type=password] {
    flex: 3;
    margin: 0.4rem 0 1rem 0;
    padding: 14px 8px; }
  .registration_form input[type=checkbox] {
    margin: 0 1rem 0 1rem; }
  .registration_form label {
    flex: 1; }

