.non-hamburger-menu {
  align-items: flex-start;
  display: flex;
  flex-wrap: nowrap; }

@media only screen and (max-width: 700px) {
  .non-hamburger-menu {
    align-items: flex-start;
    display: none;
    flex-wrap: nowrap; } }

.hamburger-menu {
  display: none;
  cursor: pointer;
  overflow: visible; }

@media only screen and (max-width: 700px) {
  .hamburger-menu {
    display: block; } }

.hamburger-button {
  display: none;
  cursor: pointer; }
  .hamburger-button:hover {
    color: #ffe170; }

@media only screen and (max-width: 700px) {
  .hamburger-button {
    display: block;
    cursor: pointer;
    float: right; } }

.bar1, .bar2, .bar3 {
  height: 4px;
  width: 32px;
  border-radius: 0;
  background-color: #efefef;
  margin: 6px 0 0 1rem;
  transition: 0.4s; }

.change .bar1 .bar2 .bar3 {
  color: #ffe170; }

.animate .nav-links {
  display: flex;
  transform: rotate(45deg) translate(-100px, 0px); }
