.register-btn {
  background-color: #880e4f;
  border-radius: 1rem;
  border-width: 0;
  box-shadow: 0 0 0.3rem 0.001rem #999;
  color: white;
  display: inline-block;
  font-size: 14px;
  letter-spacing: 2px;
  padding: 10px 16px;
  text-decoration: none;
  transition: 0.3s; }
  .register-btn:hover {
    background-color: #bc477b;
    box-shadow: 0 0 0.3rem 0.01rem #999; }
  .register-btn:visited {
    color: #fbfbfb; }

.login-btn {
  border-color: #880e4f;
  border-radius: 1rem;
  border-style: solid;
  border-width: 2px;
  color: #880e4f;
  display: inline-block;
  font-size: 14px;
  letter-spacing: 2px;
  padding: 8px 14px;
  text-decoration: none;
  transition: 0.3s; }
  .login-btn:hover {
    background-color: #f2cfd8; }
  .login-btn:visited {
    color: #880e4f; }

.primary-btn {
  background-color: #880e4f;
  border-radius: 0.4rem;
  border-width: 0px;
  box-shadow: 0 0 0.1rem 0.0001rem #999;
  color: #f0f0f0;
  display: inline-block;
  letter-spacing: 3px;
  margin: 20px 10px;
  padding: 12px 22px;
  text-decoration: none;
  transition: 0.3s; }
  .primary-btn:hover {
    background-color: #bc477b;
    box-shadow: 0 0 0.3rem 0.01rem #999; }
  .primary-btn:visited {
    color: #f0f0f0; }
