.project_image {
  border-radius: 6px;
  box-shadow: 10px 10px 40px -20px #111101;
  display: flex;
  margin-top: 1rem;
  max-width: 60%;
  max-height: 400px;
  overflow: hidden;
  padding: 0; }
  .project_image img {
    margin: 0;
    object-fit: cover;
    max-height: 100%;
    max-width: 100%;
    padding: 0;
    transition: .5s ease; }
    .project_image img:hover {
      opacity: 0.8;
      transform: scale(1.1); }

@media only screen and (max-width: 700px) {
  .project_image {
    max-width: 100%;
    max-height: 300px; } }
